import React from 'react';
import {Link} from 'react-router-dom';
import Carousel from 'react-bootstrap/Carousel'
const ScroolBar=()=>{
  
        return(
            <div style={{backgroundColor: "#fff", height: "auto"}}>
                    <Carousel>
                        <Carousel.Item>
                        <Link to="/shop">
                            <img
                            src="https://miro.medium.com/max/626/1*SmUFrmJAcaxb8k9fWoGzYA.jpeg" alt="scroolbar"
                            />
                          </Link>
                            <Carousel.Caption>
                                <h3> First Slide Label</h3>
                                <p>Scenery 1</p> 
                            </Carousel.Caption>

                        </Carousel.Item>
                        <Carousel.Item>
                        <Link to="/shop">
                            <img
                            src="https://www.eversoscrumptious.com/wp-content/uploads/2013/10/TC-Dresses.jpg" alt="banner"
                            />
                          </Link>
                            <Carousel.Caption>
                                <h3> Second Slide Label</h3>
                                <p>Scenery 2</p> 
                            </Carousel.Caption>
                        </Carousel.Item>

                        <Carousel.Item>
                        <Link to="/shop">
                            <img
                            src="https://farm1.staticflickr.com/334/18701628506_dd6c2acf94_o.png" alt="banner_2"/>
                          </Link>
                            <Carousel.Caption>
                                <h3> Second Slide Label</h3>
                                <p>Scenery 2</p> 
                            </Carousel.Caption>
                        </Carousel.Item>
                    </Carousel>

            </div>
            

        )
    }


export default ScroolBar;